@use "./blocks/variables" as*;

.additional-products {
  font-family: $title-font;
  margin-bottom: 35px;
}

.additional-products__title {
  font-family: $title-font;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  margin: 0;
  margin-bottom: 36px;
}

.additional-products__list {
  font-family: $main-font;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: $regular-color;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
}

.additional-products__wrapper {
  margin-bottom: 13px;
}

.additional-products__name {
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: $light-black;
  margin: 0;
  margin-bottom: 13px;
}

.additional-products__size,
.additional-products__price {
  margin: 0;
}

.additional-products__item {
  padding-top: 12px;
  padding-bottom: 19px;
  border-top: 1px solid #cdcdcd;
}

.additional-products__button {
  font-family: $title-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 109px;
  background-color: #68b738;
  color: $regular-background;
  text-decoration: none;
  text-transform: uppercase;
}
